import uniPopup from '@/components/uni-popup/uni-popup.vue'
import upload from '@/components/upload'
import { BASE_URL, publishActive } from '@/api'
import { mapState, mapActions } from 'vuex'
import { pathToBase64, base64ToPath } from '@/common/js/base64.js'
import { uploadImg, delUploadImg } from '@/common/js/common'
export default {
	name: '',
	data () {
		return {
			type: '',
			text: '',
			imgList: [],
			baseList: []
		}
	},
	onLoad () {
		
	},
	onShow () {
		this.$hideTabbarButton()
	},
	onNavigationBarButtonTap (e) {
		this._publishActive()
	},
	computed: {
		BASE_URL () {
			return BASE_URL
		},
	},
	methods: {
		onShareClick (type) {
			let self = this;
			if(type == 1) {   // 微信
				uni.share({
						provider: "weixin",
						scene: "WXSceneSession",
						type: 0,
						summary: self.text,
						href: 'http://www.taobao.com',
						imageUrl: self.imgList[0],
						success: function (res) {
								console.log("success:" + JSON.stringify(res));
						},
						fail: function (err) {
								console.log("fail:" + JSON.stringify(err));
						}
				});
			}
			if(type == 2) {   // 朋友圈
				uni.share({
						provider: "weixin",
						scene: "WXSenceTimeline",
						type: 0,
						summary: self.text,
						href: 'http://www.taobao.com',
						imageUrl: self.imgList[0],
						success: function (res) {
							uni.showToast({
								title: '分享成功'
							})
						},
						fail: function (err) {
								console.log("fail:" + JSON.stringify(err));
						}
				});
			}
			if(type == 3) {   // QQ空间
				uni.showToast({
					title: '暂不支持',
					icon: 'none'
				})
			}
		},
		async _publishActive () {
			if(this.text == '') {
				uni.showToast({
					title: '说点什么吧',
					icon: 'none'
				})
				return
			}
			if(this.imgList.length <= 0) {
				uni.showToast({
					title: '至少上传一张照片',
					icon: 'none'
				})
				return
			}
			let result = await publishActive ({
				token: uni.getStorageSync('token'),
				text: this.text,
				images: this.baseList.join('|')
			})
			if(result.code == 200) {
				this.type = 'middle'
			}
			console.log(result)
		},
		onAddImgEvent () {  // 上传图片
			let self = this;
			uploadImg(self.baseList, self.imgList)
		},
		onDelImgEvent (index) {
			let self = this;
			delUploadImg(self.baseList, self.imgList, index)
		},
		togglePopup () {
			this.type = '',
			uni.navigateBack()
		}
	},
	components: {
		uniPopup,
		upload
	}
}
